import { HTTP_OK, HTTP_NOT_FOUND, HTTP_BAD_REQUEST } from "../utils/response-codes";

/**
 * returns the Authenticated user
 */
export const getUser = async (email) => {
  try {
    const response = await fetch(`/data/${email}.json`);
    const user = await response.json();
    return new Promise((resolve, reject) => {
      // simulate network delay
      setTimeout(() => {
        if (user) {
          resolve({
            message: 'User details found',
            success: true,
            data: user,
            status: HTTP_OK
          });
        } else {
          reject({
            message: 'Account does not exist',
            success: false,
            data: null,
            status: HTTP_NOT_FOUND
          })
        }
      }, 2000); // simulate 2 seconds network delay
    });
  } catch (error) {
    let message = 'Account does not exist';
    let status = HTTP_NOT_FOUND;
    if (error.name == 'TypeError') {
      message = 'Network Error, check your internet connection!';
      status = HTTP_BAD_REQUEST
    }
    return new Promise((resolve, reject) => {
      // simulate network delay
      setTimeout(() => {
        reject({
          message,
          success: false,
          data: null,
          status
        })
      }, 1000); // simulate 1 seconds network delay
    });
  }
};
