import Vue from 'vue'
import localforage from "localforage";
import store from "../store";
import { getAppData } from '../utils';

export const vlf = new Vue({
  name: 'VueLocalForage',
  data: () => {
    return {
      mutations: {
        'user': 'setAuthUser'
      },
      instance: null
    };
  },
  created() {
    this.init();
    store.subscribe((mutation) => {
      switch (mutation.type) {
        case 'setAuthUser':
          this.setItem("user", mutation.payload).catch(() => { });
          break;
        case 'clearAuth':
          this.removeItem("user");
          break;
      }
    });
    this.restore();
  },
  methods: {
    init() {
      this.instance = localforage.createInstance({
        name: "thriller",
        version: 1.0,
        storeName: "datastore"
      });
      this.instance.setDriver([
        localforage.INDEXEDDB,
        localforage.WEBSQL,
        localforage.LOCALSTORAGE
      ]);
    },
    restore() {
      this.getItem('user').then(async (user) => {
        const { data, success } = await getAppData();
        if (success) {
          store.commit('setAppData', data);
        }
        if (user.remember) {
          await store.dispatch('resume', user);
        } else {
          store.commit('clearAuth');
        }
      })
        .catch(() => { })
        .finally(() => {
          store.commit('setLoading', false);
        });
    },
    getItem(key) {
      return new Promise((resolve, reject) => {
        this.instance
          .getItem(key)
          .then(value => {
            resolve(value);
          })
          .catch(err => {
            reject(err);
          });
      });
    },

    setItem(key, value) {
      return new Promise((resolve, reject) => {
        this.instance
          .setItem(key, value)
          .then(value => {
            resolve(value);
          })
          .catch(err => {
            reject(err);
          });
      });
    },

    removeItem(key) {
      return new Promise((resolve, reject) => {
        this.instance
          .removeItem(key)
          .then(() => {
            resolve();
          })
          .catch(err => {
            reject(err);
          });
      });
    },

    clear() {
      return new Promise((resolve, reject) => {
        this.instance
          .clear()
          .then(() => {
            resolve();
          })
          .catch(err => {
            reject(err);
          });
      });
    },

    length() {
      return new Promise((resolve, reject) => {
        this.instance
          .length()
          .then(numberOfKeys => {
            resolve(numberOfKeys);
          })
          .catch(err => {
            reject(err);
          });
      });
    },

    key(keyIndex) {
      return new Promise((resolve, reject) => {
        this.instance
          .key(keyIndex)
          .then(keyName => {
            resolve(keyName);
          })
          .catch(err => {
            reject(err);
          });
      });
    },

    keys() {
      return new Promise((resolve, reject) => {
        this.instance
          .keys()
          .then(keys => {
            resolve(keys);
          })
          .catch(err => {
            reject(err);
          });
      });
    },

    setDriver(driverName) {
      return this.instance.setDriver(driverName);
    },

    config(options) {
      return this.instance.config(options);
    },

    createInstance(options) {
      return this.instance.createInstance(options);
    }
  },

});

const VueLocalForage = {
  install(Vue, options) {
    if (options) {
      vlf.config(options);
    }
    Vue.prototype.$vlf = vlf;
  }
};

export default VueLocalForage;
