<template>
  <base-nav class="navbar-top navbar-dark" id="navbar-main" :show-toggle-button="false" expand>
    <ul class="navbar-nav align-items-center d-none d-md-flex ml-auto">
      <li class="nav-item dropdown">
        <base-dropdown class="nav-link pr-0" tag="a" position="right">
          <div class="media align-items-center" slot="title">
            <span class="avatar avatar-sm rounded-circle" />
            <div class="media-body ml-2">
              <span
                class="mb-0 text-sm font-weight-bold"
              >{{$store.state.user.firstName}} {{$store.state.user.lastName}}</span>
            </div>
          </div>

          <template>
            <a href="#" @click.stop.prevent="logout" class="dropdown-item">
              <i class="ni ni-lock-circle-open"></i>
              <span>Logout</span>
            </a>
          </template>
        </base-dropdown>
      </li>
    </ul>
  </base-nav>
</template>
<script>
export default {
  data() {
    return {
      activeNotifications: false,
      showMenu: false,
      searchQuery: ""
    };
  },
  methods: {
    logout() {
      this.$store.dispatch("logout");
      setTimeout(() => this.$router.push({ name: "login" }), 100);
    },
    toggleSidebar() {
      this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
    },
    hideSidebar() {
      this.$sidebar.displaySidebar(false);
    },
    toggleMenu() {
      this.showMenu = !this.showMenu;
    }
  }
};
</script>
